.add-employee-container {
    width: 80%;
    margin: 0 auto;
    background-color: #d1cece;

    // margin-top: 10px;
    // border-radius: 5px;
    .input-area {
        display: flex;
        gap: 10px;
        display: flex;
        flex-flow: row wrap;
        align-content: space-between;
        justify-content: space-between;
        margin: 10px;
    }

    .header {
        color: #fff;
        padding: 10px;
        background-color: rgb(8, 8, 15);
    }

    .footer {
        background: #eae1f3;
        padding-bottom: 10px;
        // padding-top: 10px;
        border-top-style: solid;
        border-width: 1px;
        border-top-color: antiquewhite;
        padding-left: 10px;
        display: flex;
        gap: 10px;
    }

    .employee-info {
        width: 30%;

        .control {
            display: flex;
            gap: 10px;
        }
    }

    .search-employee {
        display: flex;
        padding: 10px;
        border-radius: 5px;

        .icon-search {
            // line-height: 35px;
            font-size: 20px;
            background-color: white;
            padding: 5px;
            line-height: 10px;
        }
        .control,select{
            width: 100%;
            border: 0;
            border-radius: 0px;
            
        }
    }
}